body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background-color: rgb(252, 217, 228);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.app-header {
    margin-top: 50px;
    align-items: flex-end;
    justify-content: space-between;
}
  
.app-header h1 {
    font-size: 26px;
    color: rgb(134, 118, 206);
}
  
.app-header h2 {
    font-size: 1.2rem;
    color: rgb(206, 118, 133);
}

.form-panel {
    margin-top: 20px;
}
  
.form-panel .new-post {
    width: auto;
    flex-grow: 1;
    margin-right: 3px;
    color: rgb(206, 118, 133);
}
.post-item {
    font-size: 1.25rem;
}

.post-item button {
    width: 35px;
    height: 35px;
    margin: 3px;
    font-size: 17px;
    border: none;
    cursor: pointer;
}

.post-item button:focus {
    box-shadow: none;
    outline: none;
}

.post-item .btn-star {
    color: rgb(134, 118, 206);
}

.post-item .btn-trash {
    color: rgb(206, 118, 133);
}

.post-item .btn-trash:hover {
    color: rgb(134, 118, 206);
}

.post-item .fa-heart {
    width: 35px;
    height: 35px;
    text-align: center;
    line-height: 35px;
    font-size: 16px;
    color: rgb(206, 118, 133);
    transition: 0.3s all;
    -webkit-transform: translateX(30px);
            transform: translateX(30px);
    opacity: 0;
}

.post-item-label {
    display: block;
    line-height: 35px;
    cursor: pointer;
    -webkit-user-select: none;
            user-select: none;
    transition: 0.5s all;
    color: rgb(134, 118, 206);
}

.post-item.like .fa-heart {
    opacity: 1;
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
}

.post-item.star .post-item-label {
    color: #FFD700;
}

.post-item.star .btn-star {
    color: #eff16a;
}
.post-list {
    margin-top: 50px;
}
.post-list .list-group-item {
  padding: 20px 35px 10px 35px;
  margin-top: 10px;
}
.search-panel {
    margin: 1rem 0;
}
  
.search-panel .search-input {
    width: auto;
    flex-grow: 1;
    margin-right: 3px;
    color:  rgb(206, 118, 133);
}
.heading {
    font-size: 25px;
    color: rgb(206, 118, 133);
}

.liner {
    border-bottom: 2px solid white; 
}

.logo {
    margin-left: -10px;
    width: 25px;
    margin-right: 10px;
}

.links {
    margin-left: 445px;
    font-size: 20px;
}

.fa-github {
    font-size: 25px;
}
.app {
    margin: 0 auto;
    max-width: 800px;
}
.banner {
  background-image: url(/pink-twitter/static/media/pink-banner.95a45e7a.jpg);
  background-size: cover;
  width: 25%;
  height: 100vh;
  border-image: 
      linear-gradient(
        to bottom, 
        rgb(206, 118, 133), 
        rgba(0, 0, 0, 0)
      ) 1 100%;
}

.left { 
    float: left;  
    border-right: 3px solid;
}
  
.right {
    float: right;
    border-left: 3px solid;
}
